import React, { Component } from 'react';
class ParentalControl extends Component {
    state = {
        Data: [1, 2, 3, 4],
        newIcon: "fa-eye"
    };
    componentDidUpdate = () => {
        console.log(this.state)
    }
    pass = event => {
        const classContain = event.target.className.split(" ")[1];
        if (classContain === "fa-eye") {
            this.setState({
                newIcon: "fa-eye-slash"
            })
        }
        else {
            let newIcon = "fa-eye"
            this.setState({ newIcon })
        }
    }
    render() {
        const pin = this.state.Data.map((v, k) => {
            return (
                <input type="number" min={0} max={1} key={k} placeholder={v} />
            )
        })
        return (
            <React.Fragment>
                <div className="Parental">
                    <p>Create Pin</p>
                    <div className="Pin">
                        {pin}
                    </div>
                    <span>Enter password to set up parental code</span>
                    <div className="inputs">
                        <label htmlFor="password">Password</label>
                        <div className="input-group">
                            <input type={this.state.newIcon === "fa-eye" ? "password" : "text"} name="password" id="password" placeholder="Enter Your Password Here" />
                            <i className={`fas ${this.state.newIcon}`} onClick={this.pass}></i>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default ParentalControl;