import React, { Component } from 'react';
import Modal from "../../../../common/Modal";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
class SubAccounts extends Component {
    constructor(props) {
        super(props);
        this.state = {
            Acc: [{ name: "Helena Fischer", img: "./img/SubAccount/1.png" }, { name: "Olivia Myers", img: "./img/SubAccount/2.png" }],
            open: false,
            key: -1
        };
    }
    onOpenModal = (key, event) => {
        this.setState({ open: true, key });
    };

    onCloseModal = () => {
        this.setState({ open: false });
    };
    render() {
        const setting = {
            speed: 500,
            slidesToShow: 3,
            slidesToScroll: 1,
            responsive: [{
                breakpoint: 1336,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 1,
                    infinite: true,
                    dots: false
                }
            },
            {
                breakpoint: 1008,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    initialSlide: 2
                }
            },
            {
                breakpoint: 480,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                }
            }
            ]
        }
        const Account = this.state.Acc.map((v, k) => {
            return (
                <div className="Profile" key={k}>
                    <div className="Dp">
                        <img src={v.img} className="img-fluid" alt="" />
                    </div>
                    <p>{v.name}</p>
                    <span onClick={this.onOpenModal.bind(this, k)}>REMOVE</span>
                </div>
            )
        })
        return (
            <React.Fragment>
                <Modal open={this.state.open} h1="Remove Sub Account" msg="Are you sure you want to remove Sub Account ?" state={this.state.Acc[this.state.key]} onCloseModal={this.onCloseModal}>
                    <button className="btn">Remove</button>
                </Modal>
                <div className="container-fluid SubAccount">
                    <Slider {...setting}>
                        <div className="Profile AddAccount">
                            <div className="Dp">
                                <span>+</span>
                            </div>
                            <p>Sub Account ID</p>
                            <input type="text" />
                        </div>
                        {Account}
                        {Account}
                    </Slider>
                </div>
            </React.Fragment>
        );
    }
}

export default SubAccounts;