import React from 'react';
import CheckBox from "../../../../common/Checkbox";
import { Nofifi } from "../../../../../redux/constant";
function Notification(props) {
    const Notification = Nofifi.map((v, k) => {
        const id = v.split(" ")[0];
        return (
            <div className="content" key={k}>
                <CheckBox id={id} label={v} />
            </div>
        )
    })
    return (
        <React.Fragment>
            {Notification}
        </React.Fragment>
    )
}
export default Notification;