import React, { Component } from 'react';
import INPUTS from "../../common/InputFields";
class ProfileSetting extends Component {
    constructor(props) {
        super(props);
        this.state = {
            passIcon: "fa-eye",
            fullName: "",
            phoneNumber: "",
            email: "",
            errors: {
                fullName: "",
                phoneNumber: "",
                email: ""
            }
        };
    }
    setStateHandler = val => {
        this.setState({
            errors: val.errors,
            [val.name]: val[val.name]
        })
    }
    render() {
        return (
            <React.Fragment>
                <div className="Profile">
                    <input type="file" className="d-none" id="dp" />
                    <label htmlFor="dp"><div className="Dp"><img src="./img/dp.png" className="img-fluid" alt="" /></div></label>
                    <p>Radu Cristian</p>
                    <span>San Francisco, CA</span>
                    <form>
                        <INPUTS name="fullName" label="Full Name" state={this.state} stateHandler={this.setStateHandler} divW="49%" />
                        <INPUTS name="phoneNumber" label="Phone Number" state={this.state} stateHandler={this.setStateHandler} divW="49%" />
                        <div className="date">
                            <label htmlFor="year">Date Of Birth</label>
                            <div className="Inputs w-100">
                                <div className="Year Option">
                                    <select name="year" id="year" placeholder="yyyy"><option defaultValue>yyyy</option></select>
                                    <i className="fas fa-caret-down"></i>
                                </div>
                                <div className="Month Option">
                                    <select name="Date" id="Date"><option defaultValue>mm</option></select>
                                    <i className="fas fa-caret-down"></i>
                                </div>
                                <div className="Month Option">
                                    <select name="month" ><option defaultValue>dd</option></select>
                                    <i className="fas fa-caret-down"></i>
                                </div>
                            </div>
                        </div>
                        <INPUTS name="phoneNumber" label="Phone Number" state={this.state} stateHandler={this.setStateHandler} divW="49%" />
                        <INPUTS label="Email" name="email" type="email" divW="49%" state={this.state} stateHandler={this.setStateHandler} HandleChange={this.handleChange} />
                        <div className="inputs passWord">
                            <label htmlFor="email">Password</label>
                            <div className="input-group">
                                <input type={this.state.passIcon === "fa-eye" ? "password" : "text"} name="password" id="password" placeholder="Enter Your Password Here" onChange={this.handleChange} />
                                <i className={`fas ${this.state.passIcon}`} onClick={this.passwordHandler}></i>
                            </div>
                        </div>
                        <div className="Button-Group">
                            <button className="btn">Save</button>
                            <button className="btn">Cancel</button>
                        </div>
                    </form>
                </div>
            </React.Fragment>
        );
    }
}

export default ProfileSetting;