import React from 'react';
import Model from 'react-responsive-modal';

class Modal extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            Data: [{ name: "", img: "" }]
        }
    }
    componentWillReceiveProps = (nextProps) => {
        const temp = this.state.Data
        this.setState({
            Data: [nextProps.state !== undefined ? nextProps.state : temp]
        })
    }
    render() {
        const { onCloseModal, open } = this.props;
        const Content = this.state.Data.map((v, k) => {
            let msg = this.props.msg.split("Sub");
            return (
                <div className="col-12 Profile" key={k}>
                    <div className="Dp">
                        <img src={v.img} className="img-fluid" alt="" />
                    </div>
                    <p>{v.name}</p>
                    <span>{msg[0] + v.name + msg[1]}</span>
                    <div className="Btns">
                        {this.props.children}
                    </div>
                </div>
            )
        })
        return (
            <div>
                <Model open={open} onClose={onCloseModal} center>
                    <div className="container">
                        <div className="row">
                            <div className="col-12 heading">
                                <h5>{this.props.h1}</h5>
                                <i className="fas fa-times" onClick={onCloseModal}></i>
                            </div>
                            {Content}
                        </div>
                    </div>
                </Model>
            </div>
        );
    }
}
export default Modal;