import React, { Component } from 'react';
import Navigation from "./Navigation/Navigation";
import SettingNav from "../Navigation/SettingNav";
import PROFILE from "./Content/ProfileSetting";
import ACCOUNT from "./Content/AccountSetting/AccountSetting";
import MEMBER from "./Content/Billing";
import FEEDBACK from "./Content/Feedback";
import MENU from "../Navigation/MainNav";
import SOCIAL from "./Content/Social";
import LANGUAGE from "./Content/Language";
class Panel extends Component {
    constructor(props) {
        super(props);
        this.state = {
            active: "Profile"
        };
    }
    onNavigate = e => {
        let btns = document.querySelectorAll(".PanelNavigation");
        for (let i = 0; i < btns.length; i++) {
            let child = btns[i].childNodes;
            for (let j = 0; j < child.length; j++) {
                child[j].style.backgroundColor = "#6F797D"
            }
        }
        e.target.style.backgroundColor = "#2AABE4";
        this.setState({
            active: e.target.innerHTML.split(" ")[0]
        })
    }
    render() {
        return (
            <React.Fragment>
                <div className="container-fluid Panel-Setting" id="page-wrap">
                    <MENU display={false}/>
                    <SettingNav />
                    <div className="row PanelRow">
                        <div className="col-4 d-md-flex d-none Navi">
                            <Navigation handleClick={this.onNavigate} />
                        </div>
                        <div className="col-md-8 col-12 Content">
                            {this.state.active === "Profile" ? <PROFILE /> : ""}
                            {this.state.active === "Account" ? <ACCOUNT /> : ""}
                            {this.state.active === "Membership" ? <MEMBER /> : ""}
                            {this.state.active === "Social" ? <SOCIAL /> : ""}
                            {this.state.active === "Feedback" ? <FEEDBACK /> : ""}
                            {this.state.active === "Language" ? <LANGUAGE /> : ""}
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default Panel;