import React from 'react'
import CheckBox from "../../common/Checkbox";
import { Socail } from "../../../redux/constant"
function Social() {
    const Notification = Socail.map((v, k) => {
        const id = v.split(" ")[0];
        return (
            <div className="content" key={k}>
                <CheckBox id={k} label={v} />
            </div>
        )
    })
    return (
        <React.Fragment>
            <div className="Feedback SocialSetting">
                <div className="container">
                    <div className="row">
                        <div className="col-md-8 col-12 Head">
                            <h5 className="Same">Social Settings</h5>
                            <p>I want to connect to people who:</p>
                            {Notification}
                            <div className="Button-Group mt-5">
                                <button className="btn">Save</button>
                                <button className="btn">Cancel</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default Social;