import React from 'react';
function Checkbox(props) {
    const { id, label, name } = props;
    const OnCheckHandle = e => {
        if (e.target.checked) {
            e.target.nextSibling.childNodes[0].style.backgroundColor = "white"
        }
        else {
            e.target.nextSibling.childNodes[0].style.backgroundColor = "transparent"
        }
    }
    return (
        <React.Fragment>
            <input type="checkbox" onClick={OnCheckHandle} name={name} className="d-none" id={id} />
            <label htmlFor={id} className="terms"><div></div></label>
            <label htmlFor={id} className="modified">{label}</label>
        </React.Fragment>
    )
}
export default Checkbox;